.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #333;
  color: #fff;
  padding: 5px;
  display: flex;
  justify-content: space-between;  // 삼등분하기 위한 스타일

  &__icon {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  &__title {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
